import {render, replace} from '../framework/render.js';
import PointView from '../view/point-view.js';
import PointEditFormView from "../view/point-edit-form-view.js";

export default class PointPresenter {
  #points = null;
  #destinations = null;
  #offers = null;
  #pointListContainer= null; //   (ul)
  #pointComponent = null; //   (li)
  #pointEditFormComponent = null;//   

  #item = null;
  #escKeyHandler = null;

  constructor({points, destinations, offers, pointListContainer}) {
    this.#points = points;
    this.#destinations = destinations;
    this.#offers = offers;
    this.#pointListContainer = pointListContainer;
  }

  #renderPoints(point, destinations, offers) { /*      */
    //   ,        ()
    this.#pointComponent = new PointView({ /*   */
      point,
      destinations,
      offers,
      onPointEditClick: () => { //     
        this.#replacePointToForm();
      },
    });

    //   ,         )
    this.#pointEditFormComponent = new PointEditFormView({
      point,
      destinations,
      offers,
      onEditFormSubmit: () => {
        this.#replaceFormToPoint();
      }, //     
      onEditFormReset: () => {
        this.#replaceFormToPoint();
      },//      
    })

    render(this.#pointComponent,this.#pointListContainer);
  }

  //    __  
  #replacePointToForm() {
    replace(this.#pointEditFormComponent, this.#pointComponent);
    document.addEventListener('keydown', this.#escKeyDownHandler);
  }

  //    __  
  #replaceFormToPoint() {
    replace(this.#pointComponent, this.#pointEditFormComponent);
    document.removeEventListener('keydown', this.#escKeyDownHandler);
  }

  // ,    / (   )
  #handlePointEditClick = () => {
    this.#replacePointToForm();
  }

  // ,      (   )
  #handleEditFormSubmit = () => {
    this.#replaceFormToPoint()
  }

  // ,      (   )
  #handleEditFormReset = () => {
    this.#replaceFormToPoint();
  }

  // ,       Esc
  #escKeyDownHandler = (evt) => {
    if (evt.key === 'Escape') {
      evt.preventDefault();
      this.#replaceFormToPoint();
    }
  };

  init(item) {
    this.#item = item;
    this.#renderPoints(this.#item, this.#destinations, this.#offers);
  }
}